@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  font-family: "Arial", sans-serif;
  min-width: 320px;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  margin: 0;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
}

@media (max-width: 767px) {
  .page-header__nav {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .page-header__nav {
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;
    display: grid;
    grid-template-columns: 174px 1fr;
    gap: 16px;
  }
}
@media (min-width: 1300px) {
  .page-header__nav {
    width: 1220px;
  }
}

@media (max-width: 767px) {
  .page-header__bar {
    width: 320px;
    margin: 0 auto;
    padding: 12px 20px 13px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    align-items: center;
  }
}

.page-header__logo {
  grid-column-start: 1;
  grid-column-end: 3;
}

.page-header__toggle {
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url("../../img/icons/burger-button.svg");
  display: none;
}
@media (max-width: 767px) {
  .page-header__nav--closed .page-header__toggle {
    display: block;
  }
  .page-header__nav--opened .page-header__toggle {
    display: block;
    background-image: url("../../img/icons/");
  }
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.logo {
  display: grid;
  grid-template-columns: 34px 1fr;
  align-items: center;
  gap: 16px;
}

.logo__sign {
  justify-self: center;
}

.site-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  line-height: 1;
}
@media (max-width: 767px) {
  .site-menu {
    font-size: 20px;
    font-weight: 500;
    border-top: 1px solid #e6e6e6;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .site-menu {
    font-size: 18px;
    line-height: 24px;
    /* 133.333% */
    display: flex;
    justify-content: flex-end;
  }
}

@media (max-width: 767px) {
  .site-menu__item {
    border-bottom: 1px solid #e6e6e6;
  }
}

@media (min-width: 768px) {
  .site-menu__item--active {
    position: relative;
  }
  .site-menu__item--active::after {
    position: absolute;
    bottom: 8px;
    left: 18px;
    right: 18px;
    content: "";
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1300px) {
  .site-menu__item--active::after {
    background-color: #ffffff;
  }
}

.site-menu__link {
  display: block;
  padding: 22px 20px 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .site-menu__link {
    padding: 17px;
  }
}
@media (min-width: 1300px) {
  .site-menu__link {
    color: #ffffff;
  }
}

.page-main__header {
  padding: 28px 0 40px;
}

.page-main__heading {
  margin: 0;
  margin-bottom: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
}
@media (min-width: 768px) {
  .page-main__heading {
    margin-bottom: 0;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1300px) {
  .page-main__heading {
    font-size: 60px;
    line-height: 60px;
  }
}

.container {
  width: 280px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1220px;
  }
}

.promo {
  padding-top: 27px;
  text-align: center;
}
@media (max-width: 767px) {
  .promo {
    color: #ffffff;
    background-color: #68b738;
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 0px, rgba(104, 183, 56, 0.85) 302px, #ffffff 302px), url("../../img/background/index-background-@x1.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
  }
}
@media (min-width: 768px) {
  .promo {
    color: #000000;
    background-image: none;
    background-color: transparent;
    text-align: left;
  }
}
@media (min-width: 1300px) {
  .promo {
    position: relative;
  }
  .promo::after {
    content: "";
    position: absolute;
    top: -200px;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -2;
    background-color: rgba(104, 183, 56, 0.85);
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 0px, rgba(104, 183, 56, 0.85) 100%), url("../../img/background/index-background-720x694.jpg");
  }
}

@media (min-width: 768px) {
  .promo__container {
    position: relative;
    padding: 0 90px 526px;
  }
}
@media (min-width: 1300px) {
  .promo__container {
    min-height: 582px;
    padding: 20px 610px 95px 80px;
  }
}

.promo__heading {
  margin: 0;
  margin-bottom: 25px;
  font-size: 36px;
  line-height: 1;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
}
@media (min-width: 768px) {
  .promo__heading {
    margin-bottom: 41px;
