  }
}

.promo__text {
  margin-top: 0;
  margin-bottom: 29px;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__text {
    font-size: 20px;
    margin-bottom: 49px;
  }
}

.promo__image {
  width: 280px;
}
@media (min-width: 768px) {
  .promo__image {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }
}
@media (min-width: 1300px) {
  .promo__image {
    top: 30px;
    left: calc(50% - 162px);
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  width: 100%;
}

.content-text {
  color: #444444;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
}
.content-text-address {
  line-height: 20px;
}
@media (min-width: 768px) {
  .content-text {
    font-size: 16px;
    line-height: 24px;
  }
}

.button {
  display: inline-block;
  font-family: "Oswald", sans-serif;
  text-decoration: none;
  border: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  font-weight: 400;
  text-align: center;
  padding: 10px;
}
@media (min-width: 768px) {
  .button {
    padding: 14px 26px;
    width: auto;
    font-size: 20px;
    line-height: 1;
  }
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}

.catalog-sections {
  padding-top: 20px;
}
@media (min-width: 768px) {
  .catalog-sections {
    padding-top: 0;
  }
}
@media (min-width: 1300px) {
  .catalog-sections {
    padding-top: 82px;
  }
}

@media (min-width: 1300px) {
  .catalog-section__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.catalog-sections__item {
  position: relative;
  padding: 24px 20px 18px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .catalog-sections__item {
    padding: 77px 316px 52px 53px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__item {
    padding: 47px 55px 59px;
  }
}
.catalog-sections__item + .catalog-sections__item {
  margin-top: 20px;
}
@media (min-width: 768px) {
  .catalog-sections__item + .catalog-sections__item {
    margin-top: 30px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__item + .catalog-sections__item {
    margin-top: 0;
  }
}

.catalog-sections__heading {
  position: relative;
  min-height: 50px;
  margin: 0;
  margin-bottom: 22px;
  padding-left: 68px;
  display: flex;
  align-items: center;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .catalog-sections__heading {
    position: static;
    padding-left: 0;
    font-size: 36px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__heading {
    position: relative;
    padding-left: 162px;
    min-height: 100px;
    margin-bottom: 39px;
  }
}
.catalog-sections__heading::before, .catalog-sections__heading::after {
  position: absolute;
  content: "";
}
.catalog-sections__heading::before {
  top: 0;
  left: 0;
  background-color: #68b738;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
@media (min-width: 768px) {
  .catalog-sections__heading::before {
    width: 200px;
    height: 200px;
    top: calc(50% - 100px);
    right: 62px;
    left: auto;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__heading::before {
    width: 100px;
    height: 100px;
    top: calc(50% - 50px);
    right: auto;
    left: 0;
  }
}
.catalog-sections__heading::after {
  background-size: contain;
}

.catalog-sections__heading--slim::after {
  width: 36px;
  height: 50px;
  top: -3px;
  left: 7px;
  background-image: url("../../img/icons/cat-slim.svg");
}
@media (min-width: 768px) {
  .catalog-sections__heading--slim::after {
    width: 140px;
    height: 194px;
    top: calc(50% - 111px);
    left: auto;
    right: 92px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__heading--slim::after {
    width: 70px;
    height: 97px;
    top: calc(50% - 56px);
    left: 15px;
    right: auto;
  }
}

.catalog-sections__heading--pro::after {
  width: 67px;
  height: 28px;
  top: 11px;
  left: -9px;
  background-image: url("../../img/icons/cat-pro.svg");
}
@media (min-width: 768px) {
  .catalog-sections__heading--pro::after {
    width: 268px;
    height: 112px;
    top: calc(50% - 56px);
    left: auto;
    right: 28px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__heading--pro::after {
    width: 134px;
    height: 56px;
    top: calc(50% - 30px);
    left: -19px;
    right: auto;
  }
}

.catalog-sections__text {
  margin: 0;
  padding-bottom: 23px;
  margin-bottom: 15px;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .catalog-sections__text {
    color: #000000;
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 48px;
  }
}
@media (min-width: 1300px) {
  .catalog-sections__text {
    margin-bottom: 23px;
  }
}

.catalog-sections__link {
  font-family: "Oswald", sans-serif;
  color: #000000;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: max-content 32px;
  align-items: center;
  gap: 17px;
}
.catalog-sections__link::after {
  content: "";
  width: 24px;
  height: 10px;
  background-image: url("../../img/icons/icon_arrow.svg");
  background-position: center right;
  background-repeat: no-repeat;
  background-size: 24px 10px;
}
@media (min-width: 768px) {
  .catalog-sections__link {
    font-size: 20px;
    line-height: 20px;
  }
}

.process {
  padding-top: 20px;
  padding-bottom: 49px;
}

.process__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: #444444;
}
@media (min-width: 768px) {
  .process__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    row-gap: 36px;
    counter-reset: procces;
  }
}
@media (min-width: 1300px) {
  .process__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.process__item {
  display: grid;
  grid-template-columns: 60px 1fr;
  gap: 20px;
}
@media (min-width: 768px) {
  .process__item {
    position: relative;
    z-index: 0;
    min-height: 280px;
    padding-top: 40px;
    padding-right: 72px;
    grid-template-columns: auto;
    grid-template-rows: 80px auto;
    counter-increment: process;
  }
}
@media (max-width: 767px) {
  .process__item + .process__item {
    margin-top: 22px;
  }
}
.process__item::before {
  content: "";
  width: 60px;
  height: 60px;
  margin-top: -2px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .process__item::before {
    width: 80px;
    height: 80px;
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .process__item::after {
    content: counter(process);
    position: absolute;
    top: 0;
    right: 80px;
    z-index: -1;
    color: #f2f2f2;
    font-family: "Oswald", sans-serif;
    font-size: 280px;
    font-weight: 400;
    line-height: 1;
    /* 100% */
  }
}

.process__item--leaf::before {
  background-image: url("../../img/icons/icon_leaf.svg");
}

.process__item--powder::before {
  background-image: url("../../img/icons/icon_tub.svg");
}

.process__item--food::before {
  background-image: url("../../img/icons/icon_eat.svg");
}

.process__item--scales::before {
  background-image: url("../../img/icons/icon_alarm.svg");
}

.example {
