@media (min-width: 1300px) {
  .contacts {
    position: relative;
    display: grid;
    z-index: 0;
    min-height: 400px;
    padding: 40px 0 52px;
    grid-template-columns: 1220px;
    align-items: center;
    justify-content: center;
    background-color: transparent;
  }
}

.contacts__container {
  padding: 26px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .contacts__container {
    padding: 56px 40px;
